.main-header {
  height: 5em;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.default-link {
  color: red;
  text-decoration: none;
  font-weight: bold;
  font-size: 2.5em;
}

.default-link:hover {
  transition: color 0.3s ease-in-out;
  color: rgb(0, 0, 0);
  text-decoration: underline;
  font-weight: bold;
  font-size: 2.5em;
}

.not-found {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin-top: 1em;
  font-size: 2em;
}